const React = require('react');
const Breadcrumb = require('antd/lib/breadcrumb');

export default React.createClass({
  render() {
    return <Breadcrumb>
      <Breadcrumb.Item href="/admin"></Breadcrumb.Item>
      <Breadcrumb.Item></Breadcrumb.Item>
    </Breadcrumb>
  }
})



